<template>
  <div v-if="filmDetils" class="detils">
      <span class="iconfont iconarrowLeft headerArrow" @click="()=>$router.back()"></span>
    <!-- header start -->
    <HeaderTitle @arrowLeftTo="()=>$router.back()" v-scrollHeight>{{filmDetils.name}}</HeaderTitle>
    <!-- header end -->

    <div class="detilsLogo">
      <img :src="filmDetils.poster" alt />
    </div>
    <div class="detilsIntroduce">
      <div style="display:flex">
        <div style="width: 256px;display:flex">
          <h2 style="color:black">{{filmDetils.name}}</h2>
          <p class="detilsItem">{{filmDetils.item.name}}</p>
        </div>
        <i class="detilsGrade">
          {{filmDetils.grade}}
          <span style="font-style:normal;font-size:12px">分</span>
        </i>
      </div>
      <div style="color: #797d82;font-size:13px;position: relative;">
        <p style="margin-top:4px">{{filmDetils.category}}</p>
        <span style="margin-top:4px;display:block">{{filmDetils.premiereAt | filterTime}}上映</span>
        <i style="margin-top:4px;display:block">{{filmDetils.nation}} | {{filmDetils.runtime}}分钟</i>
        <div
          class="detilsFilmIntroduce"
          :style="{'height':arrowheadIcon?'':synopsisHeight+'px'}"
          @click="()=>arrowheadIcon=!arrowheadIcon"
        >
          <span ref="synopsis" class="synopsis">{{filmDetils.synopsis}}</span>
        </div>
        <i
          @click="()=>arrowheadIcon=!arrowheadIcon"
          class="iconfont arrowheadIcon"
          :class="arrowheadIcon?'iconxiangxiajiantou':'iconliwu-copy'"
        ></i>
      </div>
    </div>
    <!-- actors start -->
    <div class="actorsImg">
      <h3>演职人员</h3>
      <SwiperComponents :name="'actors-loop'">
        <div
          class="swiper-slide actorPic"
          v-for="(data,index) in filmDetils.actors"
          :key="data.name"
          v-swiper="{index:index,datalist:filmDetils.actors,num:3.7,space:10,name:'actors-loop'}"
        >
          <img :src="data.avatarAddress" alt />
          <p>{{data.name}}</p>
          <span>{{data.role}}</span>
        </div>
      </SwiperComponents>
    </div>
    <!-- actors end -->

    <!-- state start -->
    <div class="stateImg">
      <div class="stateHeader">
        <h3>剧照</h3>
        <div style="line-height:40px" @click="()=>detilsMaskShow=true">
          全部（{{filmDetils.photos.length}}）
          <i class="iconfont iconyoujiantou1" style="font-size:10px"></i>
        </div>
      </div>
      <SwiperComponents :name="'state-loop'">
        <div
          @click="picPreview(index)"
          class="swiper-slide statePic"
          v-for="(pho,index) in filmDetils.photos"
          :key="pho"
          v-swiper="{index:index,datalist:filmDetils.actors,num:2.4,space:10,name:'state-loop'}"
        >
          <img :src="pho" alt />
        </div>
      </SwiperComponents>
    </div>
    <!-- state end -->

    <!-- mask start -->
    <div class="detilsMask" v-show="detilsMaskShow">
      <HeaderTitle @arrowLeftTo="()=>detilsMaskShow=false">剧照 ({{filmDetils.photos.length}})</HeaderTitle>
      <ul>
        <li v-for="pho in filmDetils.photos" :key="pho">
          <img :src="pho" alt="">
        </li>
      </ul>
    </div>
    <!-- mask end -->
  </div>
</template>

<script>
import { instance } from "@/utils/http";
import { mapMutations } from "vuex";
import Vue from 'vue';
import { ImagePreview } from 'vant';

// 全局注册
Vue.use(ImagePreview);
export default {
  components: {
    SwiperComponents: () => import("@/components/swiperComponents"),
    HeaderTitle: () => import("@/components/headerTitle")
  },
  data() {
    return {
      detilsMaskShow:false,
      filmDetils: "",
      arrowheadIcon: true,
      synopsisHeight: ""
    };
  },
  methods: {
    ...mapMutations("TabbarStore", ["changeIsTabbar"]),
    picPreview(index){
      ImagePreview({
      images: this.filmDetils.photos,
      startPosition: index,
    });
    },
    oHeight(data) {
      this.synopsisHeight = data;
    }
  },
  created() {
    this.changeIsTabbar(false);
    instance(`/gateway?filmId=${this.$route.params.id}&k=3355024`, {
      headers: {
        "X-Host": "mall.film-ticket.film.info"
      }
    })
      .then(res => {
        this.filmDetils = res.data.data.film;
        this.$nextTick(() => {
          this.oHeight(this.$refs.synopsis.offsetHeight);
        });
      })
      .catch(err => {
        console.log(err);
      });
  },
  beforeDestroy() {
    this.changeIsTabbar(true);
  }
};
</script>
<style lang="scss">
.headerArrow {
  font-size: 25px;
  position: fixed;
  top: 0;
  height: 44px;
  line-height: 44px;
  border-radius: 50%;
  color: black;
}
.detilsMask{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 44px;
  z-index: 100;
  overflow-y: hidden;
  background: white;
  ul{
    display: flex;
    flex-wrap: wrap;
    li{
      width: calc((100vw - 3px)/3);
      height: calc((100vw - 3px)/3);
      margin-bottom: 1.5px;
      margin-right: 1px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
}
.stateImg {
  padding: 10px 15px;
  border-bottom: 10px solid #f4f4f4;
  border-top: 10px solid #f4f4f4;
  margin-bottom: 50px;
  .stateHeader {
    display: flex;
    justify-content: space-between;
    h3 {
      line-height: 40px;
      text-align: left;
      color: black;
      margin-bottom: 10px;
    }
  }
  .statePic {
    width: 150px;
    height: 84px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.actorsImg {
  margin-top: 10px;
  border-top: 10px solid #f4f4f4;
  text-align: center;
  width: 100%;
  padding: 15px;
  padding-top: 0;
  h3 {
    line-height: 40px;
    text-align: left;
    color: black;
    margin-bottom: 10px;
  }
  .actorPic {
    img {
      width: 85px;
      height: 120px;
    }
  }
}
.detils {
  width: 100%;
  overflow: hidden;
}
.detilsIntroduce {
  padding: 12px 15px 15px;
  .arrowheadIcon {
    transition: 0.3s;
    text-align: center;
    width: 100%;
    font-size: 8px;
    color: gray;
    text-shadow: 0 2px 5px lightgray;
    position: absolute;
    bottom: -15px;
    display: block;
    left: 50%;
    transform: translateX(-50%);
  }
  .detilsFilmIntroduce {
    transition: 0.3s;
    margin-top: 4px;
    height: 35px;
    overflow: hidden;
    position: relative;
    .synopsis {
      display: block;
    }
  }
}
.detilsLogo {
  width: 100%;
  height: 56vw;
  img {
    width: 100%;
    height: 100%;
  }
}
.detilsGrade {
  width: calc(100% - 250px);
  text-align: right;
  color: #ffb232;
  font-size: 18px;
  font-style: italic;
}
.detilsItem {
  vertical-align: middle;
  font-size: 9px;
  color: #fff;
  background-color: #d2d6dc;
  height: 14px;
  line-height: 14px;
  padding: 0 2px;
  border-radius: 2px;
}
</style>